@import 'src/styles/vars.scss';

.team {

  &__section {
    width: 100%;
    z-index: 1;
    position: relative;
    padding-bottom: 140px;


    @media (max-width: 1200px) {
      padding-bottom: 120px;
    }

    @media (max-width: 767px) {
      padding-bottom: 100px;
    }
  }

  .container {
    &:before {
      content: '';
      position: absolute;
      top: 28px;
      right: -55px;
      width: 400px;
      height: 200px;
      background-image: url('/media/images/sections/team/before.svg');
      background-size: contain;
      background-position: center center;
      background-repeat: no-repeat;

      @media (max-width: 991px) {
        content: none;
      }
    }
  }

  &__title {
    margin-bottom: 60px;

    @media (max-width: 991px) {
      margin-bottom: 40px;
    }

    @media (max-width: 600px) {
      margin-bottom: 20px;
    }
  }

  &__list {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 30px;
  }

  &__item {
    //height: 270px;
    z-index: 1;
    position: relative;
    padding: 30px 20px 20px;
    border-radius: 15px;
    background-color: $dark;
    height: 100%;
  }

  &__flex {
    display: flex;
    align-items: center;
  }

  &__image {
    width: 69px;
    height: 69px;
    //border: 2px solid $green;
    border-radius: 50%;
    margin-right: 15px;
  }

  &__name {
    width: calc(100% - 69px - 15px);
    font-weight: 700;
    font-size: 18px;
    line-height: 1.3;
    color: $another-light;
  }

  &__text {
    margin: 20px 0 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.4;
    color: $light;
  }


}